.s-alert-close::after {
  -webkit-transform: translate(-50%, -50%) rotate(-45deg);
  transform: translate(-50%, -50%) rotate(-45deg);
}

.s-alert-bottom-left {
  top: auto;
  right: auto;
  bottom: 30px;
  left: 30px;
}

.s-alert-top-left {
  top: 30px;
  right: auto;
  bottom: auto;
  left: 30px;
}

.s-alert-top-right {
  top: 30px;
  right: 30px;
  bottom: auto;
  left: auto;
}

.s-alert-bottom-right {
  /*default*/
  top: auto;
  right: 30px;
  bottom: 30px;
  left: auto;
}

.s-alert-bottom {
  width: 100%;
  max-width: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  top: auto;
}

.s-alert-top {
  width: 100%;
  max-width: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: auto;
}

.s-alert-info {
  background: #00A2D3;
  color: #fff;
}

.s-alert-success {
  background: #27AE60;
  color: #fff;
}

.s-alert-warning {
  background: #F1C40F;
  color: #fff;
}

.s-alert-error {
  background: #E74C3C;
  color: #fff;
}

[class^="s-alert-effect-"].s-alert-hide,
[class*=" s-alert-effect-"].s-alert-hide {
  -webkit-animation-direction: reverse;
  animation-direction: reverse;
}

.s-alert-box-height {
  visibility: hidden;
  position: fixed;
}

.s-alert-effect-slide .s-alert-close::before,
.s-alert-effect-slide .s-alert-close::after {
  background: #fff;
}

.s-alert-effect-slide .s-alert-close:hover::before,
.s-alert-effect-slide .s-alert-close:hover::after {
  background: #fff;
}

.s-alert-top-left.s-alert-effect-slide.s-alert-show,
.s-alert-bottom-left.s-alert-effect-slide.s-alert-show,
.s-alert-top.s-alert-effect-slide.s-alert-show,
.s-alert-bottom.s-alert-effect-slide.s-alert-show {
  -webkit-animation-name: animSlideElastic;
  animation-name: animSlideElastic;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
}

